<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input placeholder="搜索" v-model="filters.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="filterData">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!--列表-->
    <el-table :data="topics"
              highlight-current-row
              v-loading="listLoading"
              style="width: 100%;">
      <el-table-column prop="cid" label="货号" align="center" width="230">
      </el-table-column>
      <el-table-column prop="firstCategory" label="一级类别" align="center" width="230">
      </el-table-column>
      <el-table-column prop="secondCategory" label="二级类别" align="center" width="230">
      </el-table-column>
      <el-table-column prop="name" label="物品名称" align="center" width="140">
      </el-table-column>
      <el-table-column prop="price" label="单价" align="center" width="140">
      </el-table-column>
      <el-table-column prop="shelvesTime" label="上架时间" align="center" width="200">
      </el-table-column>
      <el-table-column label="有效天数" align="center" width="200">
        <template scope="scope">
          {{scope.row.validTime}}天
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" min-width="220">
        <template scope="scope">
          <el-button type="info" size="small" @click="handelShow(scope.row)"
                     v-show="rules.indexOf('01020101') >= 0">查看
          </el-button>
          <el-button type="danger" size="small" @click="handleOutLine(scope.row)"
                     v-show="rules.indexOf('01020102') >= 0">下架
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar" v-show="total > 0">
      <el-pagination layout="prev, pager, next"
                     :current-page.sync="page"
                     @current-change="handleCurrentChange"
                     :page-size="pageSize"
                     :total="total * pageSize"
                     style="float:right;">
      </el-pagination>
    </el-col>

    <!--查看界面-->
    <el-dialog title="查看" v-model="showFormVisible" :close-on-click-modal="false">
      <el-form :model="showForm" label-width="120px">
        <el-form-item label="图片：" v-show="showForm.img.length">
          <div class="block" style="width: 300px;height: 300px;">
            <el-carousel trigger="click">
              <el-carousel-item v-for="(item,index) in showForm.img" :key="index">
                <img :src="item.url" style="width: 100%;height: 100%;">
              </el-carousel-item>
            </el-carousel>
          </div>
        </el-form-item>
        <el-form-item label="视频：" v-show="!showForm.img.length">
          <video :src="showForm.video.url" controls></video>
        </el-form-item>
        <!--<el-form-item label="货号：">
          <div>{{showForm.cid}}</div>
        </el-form-item>-->
        <el-form-item label="名字：">
          <div>{{showForm.name}}</div>
        </el-form-item>
        <el-form-item label="价格：">
          <div>{{showForm.price}}</div>
        </el-form-item>
        <el-form-item label="颜色：">
          <div>{{showForm.color}}</div>
        </el-form-item>
        <el-form-item label="尺寸：">
          <div>{{showForm.size}}</div>
        </el-form-item>
      </el-form>
    </el-dialog>
  </section>
</template>

<script>
  import { getCommodityList, selectCommosityByCid, soldOutAndPutAway } from 'api/goods'

  export default {
    data () {
      return {
        filters: {
          name: ''
        },
        totalData: [],
        topics: [],
        total: 0,
        rules: sessionStorage.getItem('rules'),
        page: 1,
        pageSize: 10,
        listLoading: false,
        showFormVisible: false, // 查看界面是否显示
        showLoading: false,
        showForm: {
          cid: '',
          color: '',
          name: '',
          price: '',
          sid: '',
          size: '',
          img: [{name: '', url: ''}],
          video: {name: '', url: ''}
        }
      }
    },
    methods: {
      getTopics () {
        this.listLoading = true
        getCommodityList().then((res) => {
          if (res.success) {
            this.setTopics(res.result.data)
          } else {
            this.$confirm(res.result.error, '提示', {
              type: 'warning'
            }).then(() => {
              // do...
            })
          }
          this.listLoading = false
        })
      },
      setTopics (data) {
        this.totalData = data.reverse()
        this.total = Math.ceil(this.totalData.length / 10)
        this.topics = data.slice(this.page - 1, 10)
      },
      filterData () {
        let ret = []
        getCommodityList().then(res => {
          if (res.success) {
            this.setTopics(res.result.data)
            // 如果filters条件是空  则正常显示
            if (!this.filters.name) {
              return
            }
            // 不然按照数据显示
            this.totalData.map((item, index) => {
              for (let val in item) {
                if (ret.indexOf(val) < 0) {
                  let tmp = item[val] + ''
                  if (tmp.indexOf(this.filters.name) >= 0) {
                    ret.push(item)
                    break
                  }
                }
              }
            })
            this.setTopics(ret)
          }
        })
      },
      handleCurrentChange (val) {
        this.page = val
        let ret = []
        for (let i = (val - 1) * 10; i < 10 * val; i++) {
          if (this.totalData[i]) {
            ret.push(this.totalData[i])
          }
        }
        this.topics = ret
      },
      handleOutLine (row) {
        this.$confirm('确认下架吗?', '提示', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true
          let para = JSON.stringify({cid: row.cid, time: row.validTime})
          soldOutAndPutAway(para).then((res) => {
            this.listLoading = false
            if (res.success) {
              this.$message({
                message: '下架成功',
                type: 'success'
              })
            } else {
              this.$message.error('下架失败')
            }
            this.handleCurrentChange(1)
            this.getTopics()
          })
        }).catch(() => {
        })
      },
      handelShow (row) {
        this.listLoading = true
        let params = JSON.stringify({
          'cid': row.cid
        })
        selectCommosityByCid(params).then(res => {
          Object.assign(this.showForm, {
            img: [],
            video: {}
          }, res.result)
          this.showFormVisible = true
          this.listLoading = false
        })
      }
    },
    mounted () {
      this.getTopics()
    }
  }

</script>

<style scope lang="stylus">
  .line
    border-bottom: 1px solid #ccc
    margin-bottom: 12px
</style>



